<template>
    <header>
        <div class="logo d-none d-md-block">
            <img src="../assets/logo-shadow.png" />
            <span>{{title}}</span>
        </div>
        <div class="data">
            <div class="year-box">
                <div class="btn-group">
                    <button class="mdl-button mdl-js-button mdl-button--icon" @click="$emit('decrementYear')">
                        <i class="material-icons">chevron_left</i>
                    </button>
                    <button class="mdl-button mdl-js-button mdl-button--icon" @click="$emit('incrementYear')">
                        <i class="material-icons">chevron_right</i>
                    </button>
                </div>
                <span>{{year}}</span>
            </div>
            <div class="count-box">
                <span class="count" id="cc">{{count}}</span>
            </div>
            <div class="mdl-tooltip" data-mdl-for="cc">
                Total Count
            </div>
        </div>
    </header>
</template>

<script>
    export default {
        name: 'TitleBar',
        props: ['year', 'count'],
        data() {
            return {
                title: 'Countdown Calendar'
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    header {
        height: 64px;
        border-bottom: #e0e0e0 1px solid;
        padding: 8px 30px;
        margin-bottom: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        background-color: white;
        z-index: 99;
    }

    .logo img {
        height: 40px;
        width: 40px;
        padding: 3px;
    }

    .logo span {
        font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
        line-height: 1.2;
        font-weight: 400;
        vertical-align: middle;
        color: rgba(0,0,0,0.54);
        padding-left: 10px;
        font-size: 21px;
        cursor: default;
        letter-spacing: -0.5px;
    }

    .count {
        display: inline-block;
        padding: 6px 10px;
        background-color: rgba(0,0,0,0.05); 
        border-radius: 3px;
        font-weight: 500;
        line-height: 20px;
        cursor: default;
        min-width: 36px;
        text-align: center;
    }
    
    .data {
        width: 200px;
        color: rgba(0,0,0,0.54);
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .year-box {
        display: inline-block;
    }

    .year-box span {
        vertical-align: middle;
        font-size: 20px;
        margin-left: 4px;
        margin-right: 4px;
    }

    .btn-group button {
        margin-left: 2px;
        margin-right: 2px;
    }

    @media (max-width:767px) {
        header {
            height: 56px;
            padding: 4px 15px;
        }
        .data {
            width: 100%;
        }
    }
</style>


