<template>
    <td>
        <span v-if="day !== ''" v-bind:class="{ marked: isMarked, today: isCurrentDate }" @click="toggleMark">{{day}}</span>
        <span v-else class="blank">{{day}}</span>
    </td>
</template>

<script>
    export default {
        name: 'Day',
        props: ['day', 'today'],
        data() {
            return {
                isMarked: null
            }
        },
        computed: {
            isCurrentDate: function () {
                return this.today.isActiveMonth && this.today.isCurrentYear && (this.today.date == this.day);
            }
        },
        created: function () {
            this.isMarked = false;
        },
        methods: {
            toggleMark: function () {
                this.isMarked = !this.isMarked;
                if (this.isMarked) {
                    this.$emit('upcount');
                } else {
                    this.$emit('downcount');
                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    table td { height: 44px; }

    span.marked {
        background-color: #ffa296;
        color: white;
    }

    span.today { font-weight: 900; font-size: 18px; }

    table td span {
        padding: 8px;
        width: 100%;
        max-width: 36px;
        border-radius: 50px;
        display: inline-block;
        background-color: white;
        -webkit-transition: background-color 100ms linear;
        transition: background-color 100ms linear;
    }
    table td span:hover:not(.marked):not(.blank) {
        background-color: rgb(223, 223, 223);
        cursor: pointer;
    }

    table td span.marked:hover {
        background-color: rgba(255, 162, 150, .72);
        cursor: pointer;
    }

    @media (max-width:767px) {
        table td { height: 50px; }

        table td span { font-size: 16px; }
    }
</style>


