<template>
    <tr>
        <Day
            v-for="n in week"
            :key="n.id"
            :day="n"
            :today="today"
            @upcount="$emit('upcount')" 
            @downcount="$emit('downcount')"
        />
    </tr>
</template>

<script>
    import Day from './Day';

    export default {
        name: 'Row',
        components: {
            Day
        },
        props: [
            'week',
            'today'
        ],
        
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    
</style>


