<template>
    <div id="app">
        <TitleBar 
            v-bind:year="chosenYear"
            v-bind:count="count"
            v-on:decrementYear="decrementYear"
            v-on:incrementYear="incrementYear"
         />
        <Fab/>
        <div class="container-fluid">
            <div class="row">
                <Calendar 
                    v-bind:date="date" 
                    v-for="m in 12"
                    v-bind:key="m.id"
                    v-bind:month="m - 1"
                    v-bind:year="chosenYear"
                    v-on:upcount="upcount"
                    v-on:downcount="downcount"
                />
            </div>
        </div>
    </div>
</template>

<script>
    import TitleBar from './components/TitleBar';
    import Fab from './components/Fab';
    import Calendar from './components/Calendar';
    

    export default {
        name: 'App',
        components: {
            TitleBar,
            Fab,
            Calendar,
        },
        data() {
            return {
                chosenYear: null,
                count: null
            }
        },
        computed: {
            date: function () {
                return new Date();
            }
        },
        methods: {
            decrementYear: function () {
                this.chosenYear = this.chosenYear - 1;
            },
            incrementYear: function () {
                this.chosenYear = this.chosenYear + 1;
            },
            upcount: function () {
                this.count = this.count + 1;
            },
            downcount: function () {
                this.count = this.count - 1;
            }
        },
        created: function () {
            this.chosenYear = this.date.getFullYear();
            this.count = 0;
        }
    }
</script>

<style>
    #app {
        font-family: 'Roboto', sans-serif;
        text-rendering: geometricPrecision;
    }

    .mdl-tooltip {
        text-rendering: initial;
    }

    :focus {
        outline: none;
    }

    button:focus { outline: initial; }

    .container-fluid {
        padding-bottom: 3.6rem;
    }

    @media (max-width: 767px) {
        #app {
            text-rendering: initial;
        }
    }
</style>


