<template>
    <div class="col-12 col-md-6 col-lg-4 col-xl-3">
        <div class="cal">
            <div class="header">
                <span>{{monthName}}</span>
            </div>
            <div class="body">
                <table>
                    <tbody>
                        <tr class="weekdays">
                            <td>S</td>
                            <td>M</td>
                            <td>T</td>
                            <td>W</td>
                            <td>T</td>
                            <td>F</td>
                            <td>S</td>
                        </tr>
                        <Row :today="today" :week="computedDays.splice(0,7)" @upcount="$emit('upcount')" @downcount="$emit('downcount')" />
                        <Row :today="today" :week="computedDays.splice(0,7)" @upcount="$emit('upcount')" @downcount="$emit('downcount')" />
                        <Row :today="today" :week="computedDays.splice(0,7)" @upcount="$emit('upcount')" @downcount="$emit('downcount')" />
                        <Row :today="today" :week="computedDays.splice(0,7)" @upcount="$emit('upcount')" @downcount="$emit('downcount')" />
                        <Row :today="today" :week="computedDays.splice(0,7)" @upcount="$emit('upcount')" @downcount="$emit('downcount')" />
                        <Row v-if="((startingDay + monthLength) / 7) > 5" :today="today" :week="computedDays.splice(0,7)" @upcount="$emit('upcount')" @downcount="$emit('downcount')" />
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    import Row from './Row';

    export default {
        name: 'Calendar',
        components: {
            Row
        },
        props: ['date', 'month', 'year'],
        data() {
            return {
                months: ['January', 'February', 'March', 'April',
                    'May', 'June', 'July', 'August', 'September',
                    'October', 'November', 'December'],
                days_in_month: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],

            }
        },
        computed: {
            today: function () {
                return {
                    date: this.date.getDate(),
                    isCurrentYear: this.date.getFullYear() === this.year,
                    isActiveMonth: this.date.getMonth() === this.month
                }
            },
            startingDay: function () {
                return (new Date(this.year, this.month, 1)).getDay();
            },
            monthLength: function () {
                return this.days_in_month[this.month];
            },
            monthName: function () {
                return this.months[this.month];
            },
            computedDays: function () {
                var day = 1;
                const result = [];
                for (let i = 0; day <= this.monthLength; i++) {
                    if (i >= this.startingDay) {
                        result.push(day);
                        day++;
                    } else {
                        result.push("");
                    }
                }
                return result;
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .cal {
        width: 270px;
        height: 340px;
        margin: 0 auto;
    }

    .col-12 { margin-bottom: 25px; }

    .cal .header > span {
        color: #757575;
        font-size: 18px;
        padding-left: 12px;
        cursor: default;
    }

    table .weekdays {
        color: #757575;
        font-size: 12px;
    }

    table {
        width: 100%;
        text-align: center;
    }

    table td { height: 46px; }

    tr.weekdays td { cursor: default; }

    @media (max-width: 767px) {
        .cal {
            width: 100%;
            max-width: 375px;
            height: initial;
        }

        .col-12 { margin-bottom: 60px; }

        .cal .header > span { font-size: 20px; }
    }
</style>


